import * as bodyScrollLock from 'body-scroll-lock';

let vars = {};

vars.$document = $(document);
vars.$window = $(window);
vars.$body = $(document.body);
vars.$html = $(document.documentElement);
vars.isMobile = () => innerWidth <= 1024;
vars.isIE = () => vars.$html.hasClass('is-browser-ie');
vars.isIOS = () => vars.$html.hasClass('is-os-ios');
vars.winWidth = window.innerWidth;

/**
* –û—á–∏—Å—Ç–∏—Ç—å —Ç–µ–∫—Å—Ç –æ—Ç —Å–ø–µ—Ü—Å–∏–º–≤–æ–ª–æ–≤
* @param {string} text –û–±—è–∑–∞—Ç–µ–ª—å–Ω–æ–µ, —Å—Ç—Ä–æ–∫–∞ –¥–ª—è –æ—á–∏—Å—Ç–∫–∏
* @returns {string} –û—á–∏—â–µ–Ω–Ω–∞—è —Å—Ç—Ä–æ–∫–∞
*/
vars.clearText = (text) => {
	return text.trim().replace(/\s+/g, ' ');
};

/**
* –°–æ–∑–¥–∞—Ç—å –∫—É–∫–∏ –∑–∞–ø–∏—Å—å
* @param {string} name –û–±—è–∑–∞—Ç–µ–ª—å–Ω–æ–µ, –Ω–∞–∑–≤–∞–Ω–∏–µ –∑–∞–ø–∏—Å–∏
* @param {string} value –û–±—è–∑–∞—Ç–µ–ª—å–Ω–æ–µ, –∑–Ω–∞—á–µ–Ω–∏–µ –∑–∞–ø–∏—Å–∏
* @param {string} days –û–±—è–∑–∞—Ç–µ–ª—å–Ω–æ–µ, –≤—Ä–µ–º—è –¥–ª—è –∂–∏–∑–Ω–∏
*/
vars.setCookie = (name, value, days) => {
	let expires = '';

	if (days) {
		let date = new Date();
		date.setTime(date.getTime() + days * 24 * 60 * 60 * 1000);
		expires = `; expires=${date.toUTCString()}`;
	}

	document.cookie = `${name}=${value || ''}${expires}; path=/`;
};

/**
* –ü–æ–ª—É—á–∏—Ç—å –∫—É–∫–∏ –∑–∞–ø–∏—Å—å
* @param {string} name –û–±—è–∑–∞—Ç–µ–ª—å–Ω–æ–µ, –Ω–∞–∑–≤–∞–Ω–∏–µ –∑–∞–ø–∏—Å–∏
*/
vars.getCookie = (name) => {
	let nameEQ = `${name}=`;
	let ca = document.cookie.split(';');

	for (let i = 0; i < ca.length; i++) {
		let c = ca[i];

		while (c.charAt(0) === ' ') {
			c = c.substring(1, c.length);
		}

		if (c.indexOf(nameEQ) === 0) {
			return c.substring(nameEQ.length, c.length);
		}
	}

	return null;
};

/**
* –£–¥–∞–ª–∏—Ç—å –∫—É–∫–∏ –∑–∞–ø–∏—Å—å
* @param {string} name –û–±—è–∑–∞—Ç–µ–ª—å–Ω–æ–µ, –Ω–∞–∑–≤–∞–Ω–∏–µ –∑–∞–ø–∏—Å–∏
*/
vars.eraseCookie = (name) => {
	document.cookie = `${name}=; Max-Age=-99999999;`;
};

let dataScrollLocks;
/**
* –ë–ª–æ–∫–∏—Ä—É–µ—Ç —Å–∫—Ä–æ–ª–ª —Å—Ç—Ä–∞–Ω–∏—Ü—ã
* –ù–µ–æ–±—Ö–æ–¥–∏–º –¥–ª—è –∏—Å–ø–æ–ª—å–∑–æ–≤–∞–Ω–∏—è –º–æ–¥–∞–ª—å–Ω—ã—Ö –æ–∫–æ–Ω
* @param {boolean} state –û–±—è–∑–∞—Ç–µ–ª—å–Ω–æ–µ
* @param {string} element –û–±—è–∑–∞—Ç–µ–ª—å–Ω–æ–µ, —ç–ª–µ–º–µ–Ω—Ç –∫–æ—Ç–æ—Ä–æ–º—É –Ω—É–∂–Ω–æ —Ä–∞–∑—Ä–µ—à–∏—Ç—å —Å–∫—Ä–æ–ª–ª
* @param {string} name –ù–µ–æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ–µ, –∫–ª—é—á,
* —á—Ç–æ–±—ã –±—ã–ª–∞ –≤–æ–∑–º–æ–∂–Ω–æ—Å—Ç—å –æ—Ç–∫—Ä—ã–≤–∞—Ç—å –æ–∫–Ω–æ –ø–æ–≤–µ—Ä—Ö –¥—Ä—É–≥–æ–≥–æ –æ–∫–Ω–∞
*/
vars.lockScroll = (state, $element, name) => {
	const element = $element.get(0) ? $element.get(0) : $element;

	if (typeof dataScrollLocks === 'undefined') {
		dataScrollLocks = new Set();
	}

	let scrollLocks = dataScrollLocks;

	if (state) {
		if (typeof name === 'string') {
			scrollLocks.add(name);
		}

		bodyScrollLock.disableBodyScroll(element, {
			reserveScrollBarGap: true,
		});

		setImmediate(() => {
			vars.$html.addClass('is-lock-scroll');
		});
	} else {
		if (typeof name === 'string') {
			scrollLocks.delete(name);
		}

		bodyScrollLock.enableBodyScroll(element);

		if (!scrollLocks.size) {
			bodyScrollLock.clearAllBodyScrollLocks();

			vars.$html.removeClass('is-lock-scroll');
		}
	}
};

/**
* –°–∫—Ä–æ–ª–ª –¥–æ —ç–ª–µ–º–µ–Ω—Ç–∞
* @param {string} $container –û–±—è–∑–∞—Ç–µ–ª—å–Ω–æ–µ, —ç–ª–µ–º–µ–Ω—Ç –∫ –∫–æ—Ç–æ—Ä–æ–º—É –Ω—É–∂–Ω–æ —Å–∫—Ä–æ–ª–ª–∏—Ç—å
* @param {string|number} time –ù–µ–æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ–µ, –≤—Ä–µ–º—è —Å–∫—Ä–æ–ª–ª–∞
* @param {string|number} offset –ù–µ–æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ–µ, —Å–º–µ—â–µ–Ω–∏–µ —Å–∫—Ä–æ–ª–ª–∞ –º–æ–∂–µ—Ç –±—ã—Ç—å + –∏–ª–∏ -
*/
vars.scrollTo = ($container, time = 500, offset = 0) => {
	vars.$html.css('scroll-behavior', 'initial');
	$('html, body').stop().animate({
		scrollTop: `${$container.offset().top + parseInt(offset, 10)}`,
	}, parseInt(time, 10));

	setTimeout(() => {
		vars.$html.css('scroll-behavior', '');
	}, parseInt(time, 10) + 100);
};

let scrollDiv;

/**
* –ü–æ–ª—É—á–∏—Ç—å —Ä–∞–∑–º–µ—Ä —Å–∫—Ä–æ–ª–ª–±–∞—Ä–∞ –µ—Å–ª–∏ –æ–Ω –µ—Å—Ç—å
* @returns {number} —Ä–∞–∑–º–µ—Ä —Å–∫—Ä–æ–ª–ª–±–∞—Ä–∞
*/
vars.getScrollbarWidth = () => {
	const width = window.innerWidth - vars.$html.get(0).clientWidth;

	if (width || document.documentElement.clientHeight >= document.documentElement.offsetHeight) {
		return width;
	}

	// Document doesn't have a scrollbar, possibly because there is not enough content so browser doesn't show it
	if (!scrollDiv) {
		scrollDiv = document.createElement('div');
		scrollDiv.style.cssText = 'width:100px;height:100px;overflow:scroll !important;position:absolute;top:-9999px';
		document.body.appendChild(scrollDiv);
	}

	return scrollDiv.offsetWidth - scrollDiv.clientWidth;
};

/**
* –£–∑–Ω–∞—Ç—å –µ—Å—Ç—å –¥–æ—Å—Ç—É–ø–µ–Ω –ª–∏ —Ö–æ–≤–µ—Ä
* @returns {boolean}
*/
function hasHoverSupport() {
	let hoverSupport;

	if (vars.isIE && vars.getScrollbarWidth()) {
		// On touch devices scrollbar width is usually 0
		hoverSupport = true;
	} else if (vars.isMobile()) {
		hoverSupport = false;
	} else if (window.matchMedia('(any-hover: hover)').matches || window.matchMedia('(hover: hover)').matches) {
		hoverSupport = true;
	} else if (window.matchMedia('(hover: none)').matches) {
		hoverSupport = false;
	} else {
		hoverSupport = typeof vars.$html.ontouchstart === 'undefined';
	}

	return hoverSupport;
}

if (!hasHoverSupport()) {
	vars.$html.removeClass('has-hover').addClass('no-hover');
} else {
	vars.$html.removeClass('no-hover').addClass('has-hover');
}

/**
* –ü–µ—Ä–µ–æ–ø—Ä–µ–¥–µ–ª–µ–Ω–∏–µ –¥–æ—Å—Ç—É–ø–Ω–æ—Å—Ç–∏ —Ö–æ–≤–µ—Ä–∞
*/
function resize() {
	setTimeout(() => {
		if (vars.winWidth !== window.innerWidth) {
			if (!hasHoverSupport()) {
				vars.$html.removeClass('has-hover').addClass('no-hover');
			} else {
				vars.$html.removeClass('no-hover').addClass('has-hover');
			}

			vars.winWidth = window.innerWidth;
		}
	}, 300);
}

vars.$window.on('resize', resize);

export default vars;
