import lozad from 'lozad';

let observer;

/**
* –ú–æ–¥—É–ª—å "–û—Ç–ª–æ–∂–µ–Ω–Ω–∞—è –∑–∞–≥—Ä—É–∑–∫–∞ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–π"
* https://github.com/ApoorvSaxena/lozad.js
*/
const init = () => {
	observer = lozad('.js-lazy', {
		rootMargin: '10px 0px', // syntax similar to that of CSS Margin
		threshold: 0.1, // ratio of element convergence
		enableAutoReload: true, // it will reload the new image when validating attributes changes
		loaded(el) {
			el.classList.add('is-loaded');
		},
	});

	observer.observe();
};

/**
* –¢—Ä–∏–≥–µ—Ä –¥–ª—è –∑–∞–≥—Ä—É–∑–∫–∏ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–π –¥–æ —Ç–æ–≥–æ, –∫–∞–∫ –æ–Ω–æ –ø–æ—è–≤–∏—Ç—Å—è –≤ –Ω–∞–±–ª—é–¥–∞—Ç–µ–ª–µ –æ–±–ª–∞—Å—Ç–∏ –ø—Ä–æ—Å–º–æ—Ç—Ä–∞
* @param {string} img –û–±—è–∑–∞—Ç–µ–ª—å–Ω–æ–µ, —ç–ª–µ–º–µ–Ω—Ç img
*/
const trigger = (img) => {
	observer.triggerLoad(img);
};

export default {
	init,
	trigger,
};
