/**
* –ú–æ–¥—É–ª—å –∏—Å–ø—Ä–∞–≤–ª–µ–Ω–∏—è –±–∞–≥–æ–≤ –Ω–∞ iOs —É—Å—Ç—Ä–æ–π—Å—Ç–≤–∞—Ö
* –æ–ø—Ä–µ–¥–µ–ª—è–µ—Ç –≤—ã—Å–æ—Ç—É —ç–∫—Ä–∞–Ω–∞ –∏ –ø—Ä–∏ –ª—é–±–æ–º –∏–∑–º–µ–Ω–µ–Ω–∏–∏ –ø–µ—Ä–µ–æ–ø—Ä–µ–¥–µ–ª—è–µ—Ç –µ—ë
* –≤ —Å—Ç–∏–ª—è—Ö –∏—Å–ø–æ–ª—å–∑—É–π—Ç–µ –∫–∞—Å—Ç–æ–º–Ω—ã–µ —Å—Ç–∏–ª–∏ var(--vh)
*/
const vhFix = () => {
	if (!(!!window.MSInputMethodContext && !!document.documentMode)) {
		let vh = window.innerHeight;
		document.documentElement.style.setProperty('--vh', `${vh}px`);

		window.addEventListener('resize', () => {
			vh = window.innerHeight;
			document.documentElement.style.setProperty('--vh', `${vh}px`);
		});
	}
};

export {vhFix};
